<template>
  <div>
    <div ref="num">{{ num }}</div>
  </div>
</template>

<script type="text/javascript">
  export default {
    data () {
      return {
        num: 1
      }
    },
    mounted () {
      this.num = 2;
      console.log(this.$refs.num.innerHTML);
      this.$nextTick(() => {
        console.log(this.$refs.num.innerHTML);
      });
      this.$nextTick().then(() => {
        console.log(this.$refs.num.innerHTML);
      });
    }
  }
</script>


