<template>
  <div>
    <child ref="child" @hook:created="childCreated" @hook:mounted="childMounted"></child>
  </div>
</template>

<script type="text/javascript">
  export default {
    components: {
      Child: require('@/views/LifecycleChild').default
    },
    data () {
      return {}
    },
    methods: {
      childCreated () {
        console.log('created');
        console.log(this.$refs.child);
      },
      childMounted () {
        console.log('mounted');
        console.log(this.$refs.child);
      }
    }
  }
</script>



