import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/tick',
      name: 'NextTick',
      component: require('@/views/NextTick').default
    },
    {
      path: '/lifecycle',
      name: 'Lifecycle',
      component: require('@/views/Lifecycle').default
    },
    {
      path: '/set',
      name: 'Set',
      component: require('@/views/Set').default
    },
  ]
})



