<template>
  <div>
    <div>年龄{{ people.age }}</div>
    <button @click="add">增加</button>
  </div>
</template>

<script type="text/javascript">
  export default {
    data () {
      return {
        people: {
          name: 'zhangsan',
          sex: 'male'
        }
      }
    },
    created () {
      this.people.age = 20;
      console.log(this.people)
    },
    methods: {
      add () {
        this.people.age++;
        console.log(this.people.age)
      }
    }
  }
</script>


